@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500&display=swap);
* {
  margin: 0px;
  padding: 0px;
  color: white;
}

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  /* font-family: 'Noto Sans JP', sans-serif; */
  font-family: 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: linear-gradient(#39684f, #2d513e); */
  background-color: #000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cursor{position:fixed;top:0px;left:0px;z-index:10;contain:layout style size;pointer-events:none}.cursor:before{content:'';position:absolute;top:-24px;left:-24px;display:block;width:48px;height:48px;transform:scale(0.2);background:white;border-radius:50%}.cursor .cursor-media{display:block;height:350px;width:350px;overflow:hidden;transform:scale(0);margin:-175px 0 0 -175px;position:relative}.App{position:fixed;top:0;left:0;height:100%;width:100%;overflow:hidden}.App .scroll{padding:2vh 0}.particles{pointer-events:none;position:fixed;top:0;left:0;width:100vw;height:100vh}

.top-bar{padding:15px;padding-left:20px;font-size:20px;margin-bottom:7%}.hi{overflow:hidden}.hi h1{display:inline-block;line-height:720px;font-size:720px;font-weight:400}

.intro .part-1{margin-top:250px;margin-left:50px;width:70vw;font-size:75px;font-weight:400}

.workSection{margin-top:250px;padding-bottom:200px}.workSection .line{margin-left:50px;margin-bottom:10px;width:770px;border-width:2px;border-style:solid;border-color:white;border-left:none;border-right:none;border-bottom:none}.workSection .title{font-size:50px;font-weight:400;margin-left:600px}.workSection .project{margin-top:170px;margin-left:30vw;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;text-align:center}.workSection .project-title{font-size:60px;font-weight:500}.workSection .projectskills{width:100%;margin-top:10px;overflow:hidden}.workSection .right{margin-left:45vw}

.skillSection{display:flex;flex-direction:column;padding-bottom:200px}.skillSection .line-right{align-self:flex-end;margin-right:50px;margin-bottom:10px;width:770px;border-width:2px;border-style:solid;border-color:white;border-left:none;border-right:none;border-bottom:none}.skillSection .title{align-self:flex-end;margin-right:50px;font-size:50px;font-weight:400;margin-left:600px}.skillSection .skill-about{margin-top:150px;width:60vw;margin-left:50px;font-size:45px;margin-bottom:70px}.skillSection .skill-svg{display:flex;justify-content:space-evenly;align-items:center}

.contact-section{margin-left:50px}.contact-section .line{margin-bottom:10px;width:770px;border-width:2px;border-style:solid;border-color:white;border-left:none;border-right:none;border-bottom:none}.contact-section .title{font-size:50px;font-weight:400;margin-left:510px;margin-bottom:100px}.contact-section .contact-svg{display:flex;justify-content:space-evenly;margin-bottom:70px}.extra{height:190px;display:flex;flex-direction:row;align-items:flex-end}.extra p{margin-left:50vw;transform:translateX(-50%);font-size:20px}

.loader{background-color:#000;position:fixed;top:0;left:0;bottom:0;width:100vw;height:100vh;z-index:3;display:flex;justify-content:center;flex-direction:column;overflow:hidden}.loader .line{width:100vw;height:1px;background:white}.loader .line-black{width:100vw;height:0px;background:#000}

